import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import Header from '../components/headers';
import Elegibility from '../images/seccion2/foto_elegibility.jpg';
import Great from '../images/seccion2/foto_great.jpg';
  

const Section2 = ({data}) => (
    <Section2Wrapper>
        <Header 
            titleBlue="2. Why " 
            titleRed="participate?" 
            subtitle="Be one of the international startups selected by a jury of experts to gain the opportunity to present your project to the audience of the 400 industry leaders & desicion makers in next SiS Paris the 30 & 31th of October, 2018" 
        />
        <div className="imageDesc"><ImageDescription data={data.images[0]} image={Great} isArrowLeft={true}/></div>
        <div className="imageDesc"><ImageDescription data={data.images[1]} image={Elegibility} isArrowLeft={false}/></div>
    </Section2Wrapper>
);

export default Section2;

const Section2Wrapper = styled.div` 
    .imageDesc{
        margin-bottom: 50px;
    }
`;





