import React from 'react';
import styled from 'styled-components';
import ImageDescription from '../components/imageDescription';
import AboutFFImg from '../images/about/foto_about_fff.jpg';
import AboutSiSImg from '../images/about/foto_about_sis.jpg';

const About = ({data}) => (
    <AboutWrapper>    
        <ImageDescription data={data.images[0]} image={AboutFFImg} isArrowLeft={true}/>
        <div className="separation"/>
        <ImageDescription data={data.images[1]} image={AboutSiSImg} isArrowLeft={false}/>
    </AboutWrapper>
);

export default About;

const AboutWrapper = styled.div` 
    margin: 100px auto;

    .separation{
        margin: 25px;   
    }
`;





